'use client';

import { useState, useMemo } from 'react';
import { useParams, useRouter } from 'next/navigation';
import Link from 'next/link';
import { CardSet } from '../../../lib/types';
import { buildAnswers } from '../../../lib/game';
import { yourSets, recentSets, draftSets } from '../../../lib/mockData';
import ScoreBar from '../../../components/Play/ScoreBar';
import AnswerGrid from '../../../components/Play/AnswerGrid';

export default function PlayPage() {
  const params = useParams();
  const router = useRouter();
  const setId = params.id as string;
  
  // Find the card set by ID, fallback to cpp-basics
  const allSets = [...yourSets, ...recentSets, ...draftSets];
  const cardSet = allSets.find(set => set.id === setId) || 
    allSets.find(set => set.id === 'cpp-basics') || 
    allSets[0];
  
  const [qIndex, setQIndex] = useState(0);
  const [score, setScore] = useState(0);
  const [locked, setLocked] = useState(false);
  const [chosenIndex, setChosenIndex] = useState<number | null>(null);
  
  const currentCard = cardSet.cards[qIndex];
  const answers = useMemo(() => buildAnswers(currentCard), [currentCard]);
  
  const handleChoose = (index: number) => {
    if (locked) return;
    
    // If clicking the same answer, deselect it
    if (chosenIndex === index) {
      setChosenIndex(null);
      return;
    }
    
    // Otherwise, select the new answer
    setChosenIndex(index);
  };
  
  const handleNext = () => {
    if (chosenIndex === null) return; // Must have selected an answer
    
    // Lock the question and show results
    setLocked(true);
    
    // Add score if correct
    if (answers[chosenIndex].isCorrect) {
      setScore(prev => prev + 100);
    }
    
    // Auto-advance after a short delay to show the correct answer
    setTimeout(() => {
      if (qIndex < cardSet.cards.length - 1) {
        setQIndex(prev => prev + 1);
        setLocked(false);
        setChosenIndex(null);
      } else {
        // Show results
        setQIndex(-1);
      }
    }, 2000); // 2 second delay to show correct answer
  };
  
  const handleKeyPress = (e: React.KeyboardEvent) => {
    if ((e.key === 'Enter' || e.key === ' ') && !locked && chosenIndex !== null && qIndex >= 0) {
      handleNext();
    }
  };
  
  // Results screen
  if (qIndex === -1) {
    return (
      <div className="min-h-screen bg-slate-50 flex items-center justify-center p-4">
        <div className="bg-white rounded-lg shadow-lg p-8 max-w-md w-full text-center">
          <h1 className="text-3xl font-bold text-slate-900 mb-4">Quiz Complete!</h1>
          <div className="text-6xl font-bold text-blue-600 mb-6">{score}</div>
          <p className="text-lg text-slate-600 mb-8">Final Score</p>
          <Link
            href="/dashboard"
            className="inline-block bg-blue-600 hover:bg-blue-700 text-white font-medium px-6 py-3 rounded-lg transition-colors focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2"
          >
            Back to Dashboard
          </Link>
        </div>
      </div>
    );
  }
  
  return (
    <div className="min-h-screen bg-slate-50 p-4" onKeyDown={handleKeyPress} tabIndex={0}>
      <div className="max-w-4xl mx-auto">
        <ScoreBar 
          current={qIndex + 1} 
          total={cardSet.cards.length} 
          score={score} 
        />
        
        <div className="text-center mb-12">
          <h1 className="text-4xl md:text-5xl font-bold text-slate-900 mb-6">
            {currentCard.question}
          </h1>
        </div>
        
        <AnswerGrid
          answers={answers}
          locked={locked}
          chosenIndex={chosenIndex}
          onChoose={handleChoose}
        />
        
        <div className="mt-8 text-center">
          {!locked && chosenIndex !== null && (
            <button
              onClick={handleNext}
              className="bg-blue-600 hover:bg-blue-700 text-white font-bold text-lg px-12 py-4 rounded-xl transition-colors focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2"
            >
              {qIndex < cardSet.cards.length - 1 ? 'Next' : 'See Results'}
            </button>
          )}
        </div>
      </div>
    </div>
  );
}
