import { splitSides } from '../../lib/game';
import AnswerButton from './AnswerButton';

interface AnswerGridProps {
  answers: { text: string; isCorrect: boolean }[];
  locked: boolean;
  chosenIndex: number | null;
  onChoose: (idx: number) => void;
}

export default function AnswerGrid({ answers, locked, chosenIndex, onChoose }: AnswerGridProps) {
  const { left, right } = splitSides(answers);
  
  const getButtonState = (index: number, isCorrect: boolean) => {
    if (!locked) {
      // Show selection state when not locked
      return index === chosenIndex ? "idle" : "idle";
    }
    
    // When locked, show feedback
    if (isCorrect) return "correct";
    if (index === chosenIndex && !isCorrect) return "wrong";
    if (chosenIndex !== index && isCorrect) return "reveal-correct";
    
    return "idle";
  };
  
  return (
    <div className="grid grid-cols-1 md:grid-cols-2 gap-6 max-w-5xl mx-auto">
      {/* Left Column */}
      <div className="space-y-6">
        {left.map((answer, index) => (
          <AnswerButton
            key={`left-${index}`}
            label={answer.text}
            disabled={locked}
            state={getButtonState(index, answer.isCorrect)}
            onClick={() => onChoose(index)}
            data-testid={`answer-${index}`}
          />
        ))}
      </div>
      
      {/* Right Column */}
      <div className="space-y-6">
        {right.map((answer, index) => {
          const globalIndex = left.length + index;
          return (
            <AnswerButton
              key={`right-${index}`}
              label={answer.text}
              disabled={locked}
              state={getButtonState(globalIndex, answer.isCorrect)}
              onClick={() => onChoose(globalIndex)}
              data-testid={`answer-${globalIndex}`}
            />
          );
        })}
      </div>
    </div>
  );
}
